<template>
  <div class="navbar navbar-default topnav">
    <div class="container">
      <div class="navbar-header">
        <button type="button" class="navbar-toggle" @click="toggleNav">
          <span class="sr-only">Toggle navigation</span>
          <span class="icon-bar"></span>
          <span class="icon-bar"></span>
          <span class="icon-bar"></span>
        </button>

        <router-link to="/" class="navbar-brand">
          <span class="title">{{ logo.title }}</span>
          <img :src="logo.src" :alt="logo.title" />
        </router-link>
      </div>

      <div
        id="top-navbar-collapse"
        :class="['collapse', 'navbar-collapse', { in: showCollapsedNav }]"
      >
        <ul class="nav navbar-nav">
          <li
            v-for="(item, index) in navList"
            :class="{ active: index === activeNavIndex }"
          >
            <a href="#" @click="changeNavIndex(index)">{{ item }}</a>
          </li>
        </ul>
        <!-- 入口组件 -->
        <div class="navbar-right">
          <TheEntry />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import TheEntry from "@/components/layouts/TheEntry";
export default {
  name: "TheHeader",
  components: {
    TheEntry,
  },
  data() {
    return {
      logo: {
        src: `${this.uploadsUrl}png/vue.png`,
        title: "VuejsCaff",
      },
      navList: ["社区", "头条", "问答", "教程"],
      activeNavIndex: 0,
      showCollapsedNav: false,
    };
  },
  beforeCreate() {
    // 在当前实例上添加一个 uploadsUrl 属性
    this.uploadsUrl = "https://yp.dayanyu.com.cn/images/";
  },
  methods: {
    changeNavIndex(index) {
      this.activeNavIndex = index;
    },
    toggleNav() {
      this.showCollapsedNav = !this.showCollapsedNav;
    },
  },
};
</script>

<style scoped>
.title {
  color: #525252;
}
.navbar-default .navbar-nav > .active > a {
  background: rgba(0, 0, 0, 0.03);
}
.navbar-brand > img {
  display: inline-block;
}
</style>


